.slider__item {
  background-size: cover;
  background-position: center;

  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;

  @media (max-width: $mobile-width) {
    width: 100%;
  }
}

.slider__item--1 {
  background-image: url("../img/slide-1-desktop@1x.jpg");

  @media (min-resolution: $retina-dppx) and (min-resolution: $retina-dpi) {
    background-image: url("../img/slide-1-desktop@2x.jpg");
  }

  @media (max-width: $tablet-width) {
    background-image: url("../img/slide-1-tablet@1x.jpg");

    @media (min-resolution: $retina-dppx) and (min-resolution: $retina-dpi) {
      background-image: url("../img/slide-1-tablet@2x.jpg");
    }
  }
}

