.slider__gallery-top {
  height: 375px;
  width: 100%;

  @media (max-width: $tablet-width) {
    height: 454px;
  }

  @media (max-width: $mobile-width) {
    height: 180px;
  }
}

.slider__gallery-thumbs {
  height: 119px;
  width: 434px;
  box-sizing: border-box;

  padding: 39px 0 0;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.slider__gallery-thumbs .slider__item {
  width: 128px;

  margin-right: 25px;

  &:last-child {
    margin-right: 0;
  }
}

.slider--nojs .slider__button {
  display: none;
}

.slider__button {
  width: 52px;
  height: 52px;
  z-index: 20;
  box-sizing: border-box;

  border: 1px solid #d7d9df;
  border-radius: 50%;

  position: absolute;
  bottom: 12px;

  background-position: center;
  background-repeat: no-repeat;

  cursor: pointer;

  @media (max-width: $mobile-width) {
    display: none;
  }

  &:hover,
  &:focus,
  &:active {
    filter: brightness(70%);
  }
}

.slider__button--prev {
  transform: matrix(-1, 0, 0, 1, 0, 0);
  background-image: url("../img/arrow-icon.svg");

  @media (max-width: $tablet-width) {
    left: 63px;
  }
}

.slider__button--next {
  right: 0;
  background-image: url("../img/arrow-icon.svg");

  @media (max-width: $tablet-width) {
    right: 63px;
  }
}

.swiper-button-disabled {
  opacity: 0.5;
  cursor: default;

  &:focus {
    outline: none;
  }

  &:hover,
  &:active {
    filter: none;
  }
}
